@import url(https://fonts.googleapis.com/css?family=Bangers&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.title {
  color: #1f1919;
  margin: 0;
  text-shadow:0 0 0.2em #fff;
  font-size: 50px;
  text-align: center;
  letter-spacing: 20px;
  padding: 40px 0;
}

.container {
  width: 100%;
}
figure {
  display: flex;
  height: auto;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  margin: 0;
}

.content-figure {
  position: fixed;
  bottom: 0;
  z-index: 0;
}

.img-chuck {
  width: 100%;
  height: 100%;
  max-width: 400px;
  max-height: 500px;
  opacity: 0.6;
  -moz-opacity: 0.6;
}
.container-joke {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
} 

.content-joke {
  max-width: 800px; 
  background-color: rgba(0, 0, 0, 0.4);
  justify-content: center;
  margin-top: 40px;
  padding: 60px 0;
  z-index: 2;
}

@media screen and (max-width: 600px) {
  .content-joke{
    height: 100%;
    bottom: 0;
  }
}

.text-jokes {
  font-size: 30px;
  text-shadow: -1px 0 #BABABA, 0 1px #BABABA,
  1px 0 #BABABA, 0 -1px #BABABA;
}

.button-jokes {
  color: white;
  font-size: 20px;
  text-align: center;
  background-color: rgb(185, 51, 10);
  padding: 30px;

  text-shadow: 0px 1px 0px #000;
  box-shadow: inset 0 1px 0 #FFE5C4, 0 10px 0 #915100;
  border-radius: 50%;
}

.button-jokes:active {
  top: 10px;
  background-color: rgb(197, 28, 28);
  border: solid #C68D23 5px;
  box-shadow: inset 0 1px 0 #FFE5C4, inset 0 -3px 0 #915100;
}

.button-jokes:focus {
  outline: none;
  box-shadow: 0 0 0 3 #F78900;
}
